*,
:after,
:before {
  box-sizing: border-box;
}

.speed-bump {
  position: fixed;
  z-index: 10000;
  top: 0;
  left: 0;
  display: none;
  overflow: hidden;
  overflow-x: hidden;
  overflow-y: auto;
  width: 100%;
  height: 100%;
  outline: 0;
  background-color: rgba(0, 0, 0, 0.8);
  font-size: 14px;
  line-height: normal;
}

.speed-bump__dialog {
  position: relative;
  display: flex;
  align-items: center;
  width: auto;
  max-width: 22rem;
  min-height: calc(100% - 40px);
  margin: 20px auto;
  padding: 0 20px;
  pointer-events: none;
}

.speed-bump__content {
  position: relative;
  overflow: hidden;
  flex-direction: column;
  width: 100%;
  padding: 2rem;
  pointer-events: auto;
  outline: 0;
  background-color: #fff;
  background-clip: padding-box;
}
.speed-bump__content:focus {
  outline: 0;
}

.js-speedbump-showing {
  height: 100vh;
  overflow: hidden;
}
.js-speedbump-showing .speed-bump {
  display: block;
}